import { useEffect, useState } from "react";
import { Button, Modal, Input, Spin, Flex } from "antd";

import { formatPersianDate } from "../utils/formatPersianDate";

const AnnouncementModule = ({
    isModalOpen,
    setIsModalOpen,
    setToastifyObj,
    announcementData,
    setAddToCurrentWeek,
}) => {
    const [textAreaContent, setTextAreaContent] = useState(
        "Aloha, Nothing to see here"
    );

    // useEffect(() => {
    //     console.log("Announcement Data:", announcementData);
    // }, [announcementData]);

    useEffect(() => {
        if (announcementData.startWeekDate) {
            setTextAreaContent(`  #_      (${formatPersianDate(
                announcementData.startWeekDate
            )}  ${formatPersianDate(announcementData.endWeekDate)})

 ** ${formatPersianDate(announcementData.firstEventDate)}**
: **${announcementData?.firstEvent?.replace(/:/g, " -")}**

 ** ${formatPersianDate(announcementData.secondEventDate)}**
: **${announcementData?.secondEvent?.replace(/:/g, " -")}**${
                announcementData?.secondEvent?.split(":")[0] ===
                "    "
                    ? "\n(            )"
                    : ""
            }

 ** **: :  :

 ** **: :  : (     )

 **  **:
            **${formatPersianDate(
                announcementData.startWeekDate
            )}**                .
    **${formatPersianDate(
                announcementData.startWeekDate
            )}**          .

**   **:
https://t.me/c/1191433472/3801

**  **:
${window.location.href}`);
        }
    }, [announcementData]);

    const handleCancel = () => {
        setIsModalOpen(false);
    };

    const handleCopyMessage = async () => {
        try {
            await navigator.clipboard.writeText(textAreaContent);

            setToastifyObj(() => ({
                title: "    .",
                mode: "success",
            }));
        } catch (error) {
            console.error("Failed to copy text: ", error);

            setToastifyObj(() => ({
                title: "       .",
                mode: "error",
            }));
        }
    };

    return (
        <Modal
            open={isModalOpen}
            onCancel={handleCancel}
            footer={[
                <Flex
                    justify="space-between"
                    key={"footer-container"}
                    className="modal-footer"
                >
                    <Flex gap={8} key={"footer-right-section"}>
                        <Button
                            key="nextWeek"
                            onClick={() =>
                                setAddToCurrentWeek(
                                    (prevState) => (prevState += 7)
                                )
                            }
                            className="close-btn"
                        >
                             
                        </Button>
                        <Button
                            key="currentWeek"
                            onClick={() => setAddToCurrentWeek(0)}
                            className="close-btn"
                        >
                             
                        </Button>
                        <Button
                            key="prevWeek"
                            onClick={() =>
                                setAddToCurrentWeek(
                                    (prevState) => (prevState -= 7)
                                )
                            }
                            className="close-btn"
                        >
                             
                        </Button>
                    </Flex>
                    <Flex gap={8} key={"footer-left-section"}>
                        <Button
                            key="back"
                            onClick={handleCancel}
                            className="close-btn"
                        >
                            
                        </Button>
                        <Button
                            key="submit"
                            type="primary"
                            onClick={handleCopyMessage}
                            className="submit-btn"
                        >
                             
                        </Button>
                    </Flex>
                </Flex>,
            ]}
            width="842px"
            closeIcon={false}
            className="modal-container code-modal"
        >
            <div className="modal-header">  </div>
            <div className="modal-title">
                             
                  .
            </div>

            {textAreaContent !== "Aloha, Nothing to see here" ? (
                <Input.TextArea
                    rows={20}
                    value={textAreaContent}
                    onChange={(e) => setTextAreaContent(e.target.value)}
                    placeholder="    ..."
                    className="announcement-textarea"
                />
            ) : (
                <Spin size="large" className="loading-spinner" />
            )}
        </Modal>
    );
};

export default AnnouncementModule;
